import { EventEmitter, Input, Output } from '@angular/core';

import { convertToInt, isTypeof } from '../../utils/util';

import { PoChartType } from './enums/po-chart-type.enum';
import { PoDonutChartSeries } from './po-chart-types/po-chart-donut/po-chart-donut-series.interface';
import { PoPieChartSeries } from './po-chart-types/po-chart-pie/po-chart-pie-series.interface';

const poChartDefaultHeight = 400;
const poChartMinHeight = 200;
const poChartTypeDefault = PoChartType.Pie;

/**
 * @description
 *
 * O `po-chart`  um componente para renderizao de dados atravs de grficos, com isso facilitando a compreenso e tornando a
 * visualizao destes dados mais agradvel.
 *
 * Este componente tambm possibilita a definio das seguintes propriedades:
 *  - altura
 *  - series
 *  - tipo
 *  - ttulo
 *
 * Alm das definies de propriedades, tambm  possvel definir uma ao que ser executada ao clicar em determinado elemento do grfico
 * e outra que ser executada ao passar o *mouse* sobre o elemento.
 *
 * #### Boas prticas
 *
 * - Para que o grfico no fique ilegvel e incompreensvel, evite uma quantia excessiva de sries.
 *
 */
export abstract class PoChartBaseComponent {

  private _height?: number = poChartDefaultHeight;
  private _type: PoChartType = poChartTypeDefault;

  public readonly poChartType = PoChartType;

  /**
   * @optional
   *
   * @description
   *
   * Define a altura do grfico.
   *
   * > O valor mnimo que pode ser informado  200.
   *
   * @default `400px`
   */
  @Input('p-height') set height(value: number) {
    const intValue = convertToInt(value);
    let height: number;

    if (isTypeof(value, 'number')) {
      height = intValue <= poChartMinHeight ? poChartMinHeight : intValue;
    } else {
      height = poChartDefaultHeight;
    }

    this._height = height;

    this.rebuildComponent();
  }

  get height(): number {
    return this._height;
  }

  /**
   * @description
   *
   * Define os elementos do grfico que sero criados dinamicamente.
   *
   * > A coleo de objetos deve implementar alguma das interfaces abaixo:
   * - `PoDonutChartSeries`
   * - `PoPieChartSeries`
   */
  @Input('p-series') series: Array<PoDonutChartSeries | PoPieChartSeries>;

  /** Define o ttulo do grfico. */
  @Input('p-title') title?: string;

  /**
   * @optional
   *
   * @description
   *
   * Define o tipo de grfico.
   *
   * > Veja os valores vlidos no *enum* `PoChartType`.
   *
   * @default `PoChartType.Pie`
   */
  @Input('p-type') set type(value: PoChartType) {
    this._type = (<any>Object).values(PoChartType).includes(value) ? value : poChartTypeDefault;

    this.rebuildComponent();
  }

  get type(): PoChartType {
    return this._type;
  }

  /**
   * Evento executado quando o usurio clicar sobre um elemento do grfico.
   *
   * > Ser passado por parmetro um objeto contendo a categoria e valor da srie.
   */
  @Output('p-series-click')
  seriesClick?: EventEmitter<any> = new EventEmitter<any>();

  /**
   * Evento executado quando o usurio passar o *mouse* sobre um elemento do grfico.
   *
   * > Ser passado por parmetro um objeto contendo a categoria e valor da srie.
   */
  @Output('p-series-hover')
  seriesHover?: EventEmitter<any> = new EventEmitter<any>();

  onSeriesClick(event: any): void {
    this.seriesClick.emit(event);
  }

  onSeriesHover(event: any): void {
    this.seriesHover.emit(event);
  }

  abstract rebuildComponent(): void;

}
