import { Directive, TemplateRef } from '@angular/core';

/**
 * @usedBy PoComboComponent
 *
 * @description
 *
 * Esta diretiva permite personalizar o contedo dos itens exibidos na lista de opes do componente.
 *
 * > Quando utilizada em dispositivos *mobile* ser exibido o componente nativo.
 *
 * Para personalizar o contedo de cada item da lista deve-se utilizar a diretiva `p-combo-option-template` com `ng-template`
 * dentro da *tag* `po-combo`.
 *
 * Para obter a referncia do item atual utilize `let-option`, com isso voc ter acesso aos valores e poder personalizar sua exibio.
 *
 * Esta diretiva compe-se de dois meios para uso, de forma explcita tal como em *syntax sugar*. Veja a seguir ambos, respectivamente:
 *
 * ```
 * ...
 * <po-combo
 *   name="combo"
 *   [(ngModel)]="combo"
 *   [p-options]="options">
 *     <ng-template p-combo-option-template let-option>
 *       <option-template [option]="option"></option-template>
 *     </ng-template>
 * </po-combo>
 * ...
 * ```
 *
 * ```
 * ...
 * <po-combo
 *   name="combo"
 *   [(ngModel)]="combo"
 *   [p-options]="options">
 *     <div *p-combo-option-template="let option">
 *       <option-template [option]="option"></option-template>
 *     </div>
 * </po-combo>
 * ...
 *
 * ```
 */
@Directive({
  selector: '[p-combo-option-template]'
})
export class PoComboOptionTemplateDirective {

  // Necessrio manter templateRef para o funcionamento do row template.
  constructor(public templateRef: TemplateRef<any>) { }

}
