import { Component, Input, ViewChild, ElementRef, ChangeDetectorRef } from '@angular/core';

@Component({
  selector: 'po-grid-head',
  templateUrl: './po-grid-head.component.html',
  // styleUrls: ['./po-grid-head.component.css']
  styles: [`
  .po-grid-head-content {
    border-bottom: 2px solid #ceeaf2;
    border-left: none;
    border-right: 1px solid #eceeee;
    border-top: none;
    color: #0c9abe;
    font-family: NunitoSans-bold;
    font-size: 14px;
    height: 44px;
    line-height: 1.71;
    margin: 0;
    max-height: 44px;
    min-height: 44px;
    overflow: hidden;
    padding: 10px 16px;
    text-align: left;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .po-grid-head-center {
    text-align: center;
  }

  .po-grid-head-right {
    text-align: right;
  }

  .po-grid-head-content:focus {
    border: 1px solid #0c9abe;
    outline: none;
  }
  `]
})
export class PoGridHeadComponent {

  private _width: (string | number);

  @Input('p-align') align?: string = 'left';

  @Input('p-freeze') boolean?: boolean = false;

  @Input('p-position') position?: string = '';

  @Input('p-tab-index') tabIndex?: number = -1;

  @Input('p-width') set width(value: (string | number)) {
    this._width = value;
  }
  get width(): (string | number) {
    return this._width ? `${this._width}px` : '100%';
  }

  @Input('p-title') title?: string;

  constructor(private changeDetectorRef: ChangeDetectorRef) { }

}
