import { Input, ViewChild } from '@angular/core';

import { browserLanguage, poLocaleDefault } from './../../../utils/util';

import { PoBreadcrumb } from '../../po-breadcrumb/po-breadcrumb.interface';
import { PoPageContentComponent } from '../po-page-content/po-page-content.component';
import { PoPageDetailLiterals } from './po-page-detail-literals.interface';

export const poPageDetailLiteralsDefault = {
  en: <PoPageDetailLiterals> {
    back: 'Back',
    edit: 'Edit',
    remove: 'Remove'
  },
  es: <PoPageDetailLiterals> {
    back: 'Volver',
    edit: 'Editar',
    remove: 'Eliminar'
  },
  pt: <PoPageDetailLiterals> {
    back: 'Voltar',
    edit: 'Editar',
    remove: 'Remover'
  },
  ru: <PoPageDetailLiterals> {
    back: '',
    edit: '',
    remove: ''
  }
};

/**
 * @description
 *
 * O componente **po-page-detail**  utilizado como container principal para a tela de detalhamento de um registro.
 * Por padro possui 3 aes, cada ao na tela executa uma funo no componente que est utilizando o po-page-detail,
 * so elas:
 *  - Voltar (funo: back);
 *  - Editar (funo: edit);
 *  - Remover (funo: remove);
 *
 * Caso no estiver implementado alguma funo, listado anteriormente, o mesmo no ser apresentado.
 */
export class PoPageDetailBaseComponent {

  private _literals: PoPageDetailLiterals;
  private _title: string;

  @ViewChild(PoPageContentComponent, { static: true }) poPageContent: PoPageContentComponent;

  /** Objeto com propriedades do breadcrumb. */
  @Input('p-breadcrumb') breadcrumb: PoBreadcrumb;

  /**
   * @optional
   *
   * @description
   *
   * Objeto com as literais usadas no `po-page-detail`.
   *
   * Existem duas maneiras de customizar o componente, passando um objeto com todas as literais disponveis:
   *
   * ```
   *  const customLiterals: PoPageDetailLiterals = {
   *    edit: 'Edio',
   *    remove: 'Excluso',
   *    back: 'Menu'
   *  };
   * ```
   *
   * Ou passando apenas as literais que deseja customizar:
   *
   * ```
   *  const customLiterals: PoPageDetailLiterals = {
   *    remove: 'Excluir registro permanentemente'
   *  };
   * ```
   *
   * E para carregar as literais customizadas, basta apenas passar o objeto para o componente.
   *
   * ```
   * <po-page-detail
   *   [p-literals]="customLiterals">
   * </po-page-detail>
   * ```
   *
   *  > O objeto padro de literais ser traduzido de acordo com o idioma do browser (pt, en, es).
   */
  @Input('p-literals') set literals(value: PoPageDetailLiterals) {
    if (value instanceof Object && !(value instanceof Array)) {
      this._literals = {
        ...poPageDetailLiteralsDefault[poLocaleDefault],
        ...poPageDetailLiteralsDefault[browserLanguage()],
        ...value
      };
    } else {
      this._literals = poPageDetailLiteralsDefault[browserLanguage()];
    }
  }
  get literals() {
    return this._literals || poPageDetailLiteralsDefault[browserLanguage()];
  }

  /** Ttulo da pgina. */
  @Input('p-title') set title(title: string) {
    this._title = title;
    this.poPageContent.recalculateHeaderSize();
  }

  get title() {
    return this._title;
  }

}
